import { Box, Grid, GridItem, Heading } from "@chakra-ui/react"
import Card1 from "./card1"
import Card2 from "./card2"
import Card3 from "./card3"
import Card4 from "./card4"

function Project(){
    return(
        <Box  width={"100%"} h="100%" bgColor="gray.700" id="projects" >
            <Heading pt={"4vh"} margin={"auto"} width={"30vh"} fontWeight="semibold" borderBottom={"3px solid white"} color={"white"}>My Projects</Heading>
            <Grid m={"10vh auto"} gridTemplateColumns={"repeat(2,1fr)"} gap={10} >
                <GridItem>
                    <Card1  />
                </GridItem>
                <GridItem>
                    <Card3 />
                </GridItem>
                <GridItem>
                    <Card2 />
                </GridItem>
                <GridItem>
                    <Card4 />
                </GridItem>
            </Grid>
        </Box>
    )
}

export default Project